import Vue from "vue";

// set1. 引入VueRouter
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import About from "../views/About.vue";
import Info from "../views/Info.vue";
import ThreejsModels from "../views/ThreejsModels.vue";

// set2. 使用use註冊VueRouter插件
Vue.use(VueRouter);

// set3. 創建路由對象
const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/About",
    name: "About",
    component: About
  },
  {
    path: "/Info",
    name: "Info",
    component: Info
  },
  {
    path: "/ThreejsModels",
    name: "ThreejsModels",
    component: ThreejsModels
  }
];

const router = new VueRouter({
  routes
});

export default router;
