<script>
import $ from "jquery";
export default {
  methods: {
    fixed() {
      var scrollTop = document.body.scrollTop; //設定背景元素的位置
      $("#app").attr("data-top", scrollTop);
      var contentStyle = document.getElementById("app").style; //content是可以滾動的背景元素id名稱
      contentStyle.position = "fixed"; //contentStyle是第二步的變數，設定背景元素的position屬性為‘fixed'
      // contentStyle.top = "-" scrollTop "px";
    },
    fixed_cancel() {
      var contentStyle = document.getElementById("app").style;
      var scrollTop = $("#app").attr("data-top"); //設定背景元素的位置
      contentStyle.top = "0px"; //恢復背景元素的初始位置
      contentStyle.position = "static"; //恢復背景元素的position屬性（初始值為absolute，就恢復為absolute，以此類推）
      $(document).scrollTop(scrollTop); //scrollTop,設定滾動條的位置
    }
  },
  mounted() {
    $(function() {
      var isLateralNavAnimating = false;

      //open/close lateral navigation
      $(".cd-nav-trigger").on("click", function(event) {
        event.preventDefault();
        //stop if nav animation is running
        if (!isLateralNavAnimating) {
          if ($(this).parents(".csstransitions").length > 0)
            isLateralNavAnimating = true;

          $("body").toggleClass("navigation-is-open");
          $(".cd-navigation-wrapper").one(
            "webkitTransitionEnd otransitionend oTransitionEnd msTransitionEnd transitionend",
            function() {
              //animation is over
              isLateralNavAnimating = false;
            }
          );
        }
      });
    });
  }
};
</script>

